@import url("https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,500;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Patrick+Hand+SC&display=swap");

.container {
  padding-top: 2rem;
  min-height: 100vh;
}

.light-mode {
  font-family: "Mulish";
}
.light-mode .about_image {
  width: 300px;
  height: 300px;
  border: 1rem solid #cccc;
  border-radius: 50%;
}
.light-mode .exp_image {
  border-radius: 50%;
  padding: 5%;
  width: 100px;
  height: 100px;
}
.light-mode .navbar,
.light-mode .navbar-brand {
  font-weight: bolder;
  font-size: 1.5rem;
}
.light-mode .navbar-toggler,
.light-mode .navbar-toggler:focus,
.light-mode .navbar-toggler:active {
  border: none;
  outline: none;
}
.light-mode .display-6 {
  font-size: 2rem;
  font-weight: bold;
  font-family: "patrick hand sc";
}
.light-mode .social-icons .social-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 3.5rem;
  width: 3.5rem;
  background-color: #495057;
  color: #fff;
  border-radius: 100%;
  font-size: 1.5rem;
  margin-right: 1.5rem;
}
.light-mode .social-icons .social-icon:last-child {
  margin-right: 0;
}
.light-mode .social-icons .social-icon:hover {
  background-color: #bd5d38;
}
.light-mode .list-inline-item {
  font-size: 5rem;
  padding: 1rem;
  color: gray;
}
.light-mode .certi-item,
.light-mode .certi-item a {
  font-size: 1.5rem;
  margin: 1rem;
  color: teal;
}
.light-mode .c {
  position: absolute;
  left: 50%;
  margin-left: -150px;
}
.light-mode .about-div {
  width: 100%;
  height: 300px;
  margin: 5% 0%;
}

.dark-mode {
  font-family: "Mulish";
  color: #d1cdc7;
  background-color: #181a1b;
  border-color: #736b5e;
  color: #e8e6e3;
}

.dark-mode .card-body {
